* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* body::-webkit-scrollbar {
  display: none;
} */

body::-webkit-scrollbar {
  width: 0.4em;
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.00);
} 

 body::-webkit-scrollbar-thumb{
  background-color: rgba(0,0,0,.1);
    outline: 1px solid slategrey;
}
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 20px;
  width:300px;
  height: 400px;
  text-align: center;
  margin: 50px auto 100px;
}
.email{
  position: relative;
  margin: 40px 130px 0 0;
  font-size: 14px;
}
.password{
  position: relative;
  margin: 40px 155px 0 0;
  font-size: 14px;
}
.input1{
margin: 30px auto 10px;
}
.input2{
  margin: 30px auto 2px;
  }
  .icons{
    border-radius: 50px 50px 50px 50px;
    width: 35px;
    height: 35px;
    margin: 5px 5px 5px 5px;
    padding: 3px 0 3px 0;
  }
.form-control{
  
  border-radius: 10px;
  width: 200px;
  padding: 7px;
}
.buttonIn {
  width: 300px;
  position: relative;
}
#eye{
  margin: 0px;
            padding: 0px;
            width: 100%;
            outline: none;
            height: 30px;
            border-radius: 5px;
}
.eye{
  background-color:white;
  border-color: white;
  position: absolute;
            top: 0;
            border-radius: 5px;
            right: 0px;
            z-index: 2;
            border: none;
            top: 2px;
            height: 30px;
            cursor: pointer;
            color: white;
            background-color: #1e90ff;
            transform: translateX(2px);
  
  
}
#Forget{
  font-size: 12px;
  margin: 0 0 10px 130px;
}
.loginform-button{
  background-color: blue;
  color: aliceblue;
 
  border-radius: 8px;
  position: relative;
  width: 100px;
  padding: 5px;
  margin: 15px auto 20px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.loginform-button:hover{
  background-color: rgb(8, 8, 186);
}


